/* width */
.mfa-container .mnu-itms-list::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
.mfa-container .mnu-itms-list::-webkit-scrollbar-track {
  background: #23282D; 
}
 
/* Handle */
.mfa-container .mnu-itms-list::-webkit-scrollbar-thumb {
  background: #7957D5; 
}

/* Handle on hover */
.mfa-container .mnu-itms-list::-webkit-scrollbar-thumb:hover {
  background: #7957D5; 
}

.mfa-container .rfip {
  margin: 0px;
  border-color: rgba(0, 0, 0, 0.23);
}

.mfa-container .rfipbtn {
  min-height: 56px;
  border-radius: 4px;
  border-color: rgba(0, 0, 0, 0.23);
}